.container {
  .loading {
    z-index: 10;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: 1s;
    background: #111;
    opacity: 0.9;


    .cloud {
      top: 100px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 320px;
      ;
      height: 100px;
      background: #999;
      border-radius: 100px;

      p {
        text-align: center;
        position: relative;
        z-index: 2;
        font-weight: 700;
        font-size: 20px;
      }

      &::before {
        content: '';
        top: -50px;
        left: 50px;
        position: absolute;
        width: 110px;
        height: 110px;
        background: #999;
        border-radius: 50%;
        box-shadow: 90px 0 0 30px #999;
      }

      .rain {
        position: relative;
        display: flex;
        z-index: 1;
        padding: 0 20px;

        span {
          position: relative;
          bottom: 10px;
          width: 5px;
          height: 15px;
          background: #999;
          margin: 0 5px;
          border-radius: 50%;
          animation: rain 5s linear infinite;
          animation-duration: calc(15s / var(--i));

        }
      }
    }

    @keyframes rain {
      0% {
        transform: translateY(0) scale(1);
      }

      70% {
        transform: translateY(300px) scale(1);
      }

      100% {
        transform: translateY(300px) scale(0);
      }

    }
    }

    .inner {
      .gallery_box {
        margin-bottom: 200px;

        .top_box {
          display: flex;
          max-width: 1450px;
          margin: 0 auto;

          >div {
            height: 800px;
            width: 50%;

            &:first-child {
              .text_box {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                margin-left: 40%;
                width: 60%;
                height: 400px;
                background: #fff;
                padding: 30px;
                box-sizing: border-box;

                h2 {
                  font-size: $fontXXXL;
                  margin-bottom: 30px;
                }

                p {
                  font-size: $fontL;
                }
              }

              .pic {
                position: relative;
                right: 20%;
                height: 400px;
                width: 120%;
              }
            }

            &:last-child {
              .con1 {
                height: 20%;
                width: 40%;
              }

              .con2 {
                height: 50%;
              }

              .con3 {
                height: 30%;
              }
            }
          }
        }
      }

      .searchBox {
        text-align: center;
        margin-bottom: 20px;

        input {
          border: none;
          width: 300px;
          height: 50px;
          padding: 10px;
          box-sizing: border-box;
        }

        button {
          width: 80px;
          height: 50px;
          border: none;
          background: $pointColor;
          font-weight: 700;
        }
  }

  .frame {
    transition: transform 2s, opacity 2s;
    transform: translateY(200px);
    opacity: 0;
    position: relative;

    &.on {
      transform: translateY(0px);
      opacity: 1;
    }

    &::after {
      content: '';
      display: block;
      clear: both;
    }

    article {
      width: 25%;
      padding: 10px;
      float: left;
      margin-bottom: 150px;

      .inner {
        width: 80%;
        padding: 10px;


        box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.1);

        .pic {
          width: 250px;
          margin-bottom: 5px;
          height: 320px;
          display: flex;
          justify-content: end;
          position: relative;
          top: 23px;

          img {
            border-top-left-radius: 40%;
            width: 100%;
            position: relative;

            right: -50px;
          }
        }

        h2 {
          height: 70px;
          font-size: $fontXL;
          top: 20px;
          left: 20px;
          font-family: 'Roboto';
          color: #111;

        }
      }
      }
      }
      }
      }

      @media screen and (max-width: $tablet) {
        .container {
          .inner {
            .gallery_box {
              margin-bottom: 100px;

              .top_box {
                flex-direction: column;

                >div {
                  height: auto;
                  width: 50%;

                  &:first-child {
                    width: 100%;
                    display: flex;

                    .text_box {
                      height: 400px;
                      margin-left: 0%;
                      width: 100%;
                    }

                    .pic {
                      width: 100%;
                      right: 0;
                    }
                  }

                  &:last-child {
                    display: flex;
                    width: 100%;

                    .con1 {
                      width: 33.3333%;
                      height: 200px;
                    }

                    .con2 {
                      width: 33.3333%;
                      height: 200px;
                    }

                    .con3 {
                      width: 33.3333%;
                      height: 200px;
                    }
                  }
                }
              }
            }

            .frame {
              transition: transform 2s, opacity 2s;
              transform: translateY(200px);
              opacity: 0;
              position: relative;

              &.on {
                transform: translateY(0px);
                opacity: 1;
              }

              &::after {
                content: '';
                display: block;
                clear: both;
              }

              article {
                width: 33.3333%;
                padding: 10px;
                float: left;
                margin-bottom: 50px;

          .inner {
            width: 100%;
.pic {
  width: 100%;
  height: 250px;
  top: 0;

  img {
    width: 100%;
    right: 0;
  }
}
}
}
}

}
}
}

@media screen and (max-width: $mini) {
  .container {
    .inner {


      .frame {
        transition: transform 2s, opacity 2s;
        transform: translateY(200px);
        opacity: 0;
        position: relative;

        &.on {
          transform: translateY(0px);
          opacity: 1;
        }

        &::after {
          content: '';
          display: block;
          clear: both;
        }

        article {
          width: 50%;
          padding: 10px;
          float: left;
          margin-bottom: 50px;

          .inner {
            width: 100%;

            .pic {
              width: 100%;
              height: 250px;
              top: 0;

              img {
                width: 100%;
                right: 0;
                }
                }
                }
                }
                }

                }
  }
  }

@media screen and (max-width: $mobile) {
  .container {
    .inner {
      .gallery_box {


        .top_box {
          >div {
            height: 800px;
            width: 50%;

            &:first-child {
              flex-direction: column;
              height: auto;

              .text_box {
                padding: 40px;
                height: 200px;

                h2 {
                  margin: 10px 0;
                  display: none
                  }
                  }

                  .pic {
                    height: 200px;
                  }
                  }

                  &:last-child {
                    height: auto;

                    .con1,
                    .con2,
                    .con3 {
                      height: 100px;
                    }
                  }
                  }
                  }
                  }

                  .searchBox {
                    display: flex;
                  }

                  .frame {

                    article {
                      width: 100%;
                      padding: 0px;

                      .inner {
                        .pic {
                          width: 80%;
                          margin: 0 auto;
                          height: 200px;
                        }
                      }
        }
      }
    }
  }
}